import React from 'react'
import { Link } from 'react-router-dom'

export const Navbar = () => {
    return (
        <div>
            <nav className="navbar navbar-expand-sm navbar-dark bg-dark">
                <div className="container">
                    <Link className="navbar-brand m-2" to="/react-todo">My Blog</Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <Link className="nav-link active m-2" aria-current="page" to="/react-todo">Posts</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link m-2" to="/Signup">Signup</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link m-2" to="/Signin">SignIn</Link>
                            </li>
                          
                        </ul>
                    </div>
                </div>
            </nav>
        </div>
    )
}
